import React from 'react';
import ReactDOM from 'react-dom';

import ons from 'onsenui';

import {Page, Toolbar, List, ListHeader, ListItem} from 'react-onsenui';

class MoneyListPage extends React.Component {
    gotoComponent(component) {
        this
            .props
            .navigator
            .pushPage({comp: component});
    }
    renderToolbar() {
        return (
            <Toolbar>
                <div className='center'>List</div>
            </Toolbar>
        );
    }

    renderRow(row, navigator) {
        console.log(navigator);
        return (
            <ListItem
                navigator={navigator}
                tappable
                onClick={this
                .gotoComponent
                .bind(this, row.comp)}>
                <div className='center'>
                    {row.label}
                </div>
            </ListItem>
        );
    }
    render() {
        return (
            <Page renderToolbar={this.renderToolbar}>
                <List
                    dataSource={[
                    {
                        comp: "MoneyReceipt",
                        label: "Tính hóa đơn"
                    }, {
                        comp: "MoneyCounter",
                        label: "Tính tiền hàng"
                    }
                ]}
                    renderRow={this
                    .renderRow
                    .bind(this, this.props.navigator)}
                    renderHeader={() => <ListHeader>Money</ListHeader>}/>
            </Page>
        );
    }
};

module.exports = MoneyListPage;


